import React, { useState, useEffect, forwardRef, useImperativeHandle  } from 'react';

import EnemyCard from './enemyCard.js';
import cards from '../utils/card-data.js';

const EnemyDeck = forwardRef((props, ref) => {

	const [hasCopiedLink, setHasCopiedLink] = useState(false);
	const [inputValue, setInputValue] = useState('');
	const [cardArrayPositions, setCardArrayPositions] = useState([]);
	const [isDeckTransparent, setIsDeckTransparent] = useState(false);
	const [highlightedCardNum, setHighlightedCardNum] = useState(null);

	useEffect(() => {		

		if (props.deckNum === 2 && hasCopiedLink) {						

			let foundRepeatedCard = false;
			
			let firstDeck = props.otherDecksRef[0].current.getCurrentDeck();

			for (let i = 0; i < cardArrayPositions.length; i ++) {
				for (let j = 0; j < firstDeck.length; j ++) {
					if (cardArrayPositions[i] === firstDeck[j]) {
						setIsDeckTransparent(true);
						setHighlightedCardNum(cardArrayPositions[i]);						
						props.otherDecksRef[0].current.highlightCard(cardArrayPositions[i]);
						foundRepeatedCard = true;						
						break;
					}					
				}

				if (foundRepeatedCard) break;
			}
		}
	});

	useImperativeHandle(ref, () => ({
		
		getCurrentDeck: () => {
			return cardArrayPositions;
		},

		highlightCard: (cardNum) => {
			setIsDeckTransparent(true);
			setHighlightedCardNum(cardNum);
		}
	}));

	const cardIdToCardPosition = (id) => {		
		
		for (let i = 0; i < cards.items.length; i ++) {						
			if (cards.items[i].id === id) {				
				return i;
			}
		}
	}

	const validateInputLink = (value) => {		
		
		if (value.search('https://link.clashroyale.com/deck/') === -1) return;

		let sepPos = value.search('=');

		if (sepPos === -1) return;

		let cardIds = value.substring(sepPos + 1).split(';').map(Number);		
		let cap = cardIds.map(id => cardIdToCardPosition(id));		

		setHasCopiedLink(true);
		setCardArrayPositions(cap);
	}	

	return(
			<div style={{backgroundColor: '#df7177', height: '180px', margin: '15px', padding: '10px'}} className="row roundSquare">
				{ hasCopiedLink ?
					<div className="row">
						<div style={{textAlign: 'center'}} className="row">
							<EnemyCard position={cardArrayPositions[0]} transparent={isDeckTransparent && cardArrayPositions[0] !== highlightedCardNum} />
							<EnemyCard position={cardArrayPositions[1]} transparent={isDeckTransparent && cardArrayPositions[1] !== highlightedCardNum} />
							<EnemyCard position={cardArrayPositions[2]} transparent={isDeckTransparent && cardArrayPositions[2] !== highlightedCardNum} />
							<EnemyCard position={cardArrayPositions[3]} transparent={isDeckTransparent && cardArrayPositions[3] !== highlightedCardNum} />
						</div>
						<div style={{textAlign: 'center'}} className="row">
							<EnemyCard position={cardArrayPositions[4]} transparent={isDeckTransparent && cardArrayPositions[4] !== highlightedCardNum} />
							<EnemyCard position={cardArrayPositions[5]} transparent={isDeckTransparent && cardArrayPositions[5] !== highlightedCardNum} />
							<EnemyCard position={cardArrayPositions[6]} transparent={isDeckTransparent && cardArrayPositions[6] !== highlightedCardNum} />
							<EnemyCard position={cardArrayPositions[7]} transparent={isDeckTransparent && cardArrayPositions[7] !== highlightedCardNum} />
						</div>
					</div>
				 : ''}
				{ !hasCopiedLink ?
					<div style={{textAlign: 'center'}} className="row">										
						<input className="copyLinkInput" style={{width: '96%', marginBottom: '15px'}}ype="text" placeholder="Paste here deck link" onChange={event => validateInputLink(event.target.value)} />						
					</div>
					
				: ''}
				
			</div>
		);
});


export default EnemyDeck;