import React from 'react';

import blueCrownImage from '../img/blueCrown.png';
import redCrownImage from '../img/redCrown.png';

const PlayerSelector = () => {
	return(
		<div style={{marginTop: '15px'}} className="row">
			{/* #3cd */}
			<div style={{textAlign: 'center', backgroundColor: '#999', borderRadius: '10px'}} className="six columns">
				<img style={{width: '25%', filter: 'grayscale(1.0)'}} src={blueCrownImage} />
			</div>
			<div style={{textAlign: 'center', backgroundColor: '#d3454e', borderRadius: '10px'}} className="six columns">
				<img style={{width: '25%', filter: 'grayscale(0.0)'}} src={redCrownImage} />
			</div>
		</div>
	);
}


export default PlayerSelector;