import React from 'react';

import cards from '../utils/card-data.js';

const EnemyCard = (props) => {
	
	const cardStyle = {
		width: '20%'
	}

	return (
		<span>
			<img style={{width: '20%', opacity: props.transparent ? '0.5' : '1.0'}} src={cards['items'][props.position]['iconUrls']['medium']} />
		</span>
	);
}


export default EnemyCard;